html,
body {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    font-family: 'Merriweather';
    line-height: normal;
    color: $gray;
    height: 100%;
    min-height: 100%;
}

body {
    overflow: visible;
    overflow-y: scroll; /* has to be scroll, not auto */
    -webkit-overflow-scrolling: touch;
}

#site-wrapper {
    clear: both;
    *zoom: 1;

    display: block;
    width: 100%;
    overflow: visible;
    position: relative;
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

#site-wrapper:before {
    content: '';
    display: table;
}

#site-wrapper:after {
    content: '';
    display: table;
    clear: both;
}

#site-wrapper::before,
    #site-wrapper::after {
    content: '';
    clear: both;
    display: block;
}

.section.container {
    position: relative;
}

.section {
    position: relative;
}

.icon {
    display: inline-block;
}

